import { useState, useContext } from "react";
import { useHistory, Link } from "react-router-dom";
import { DataContext } from "../../store";
import "./styles.scss";

function Login() {
  const history = useHistory();
  const { user, setUser } = useContext(DataContext);
  const [formData, setFormData] = useState({ name: "", password: "" });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleLogin = (e) => {
    e.preventDefault();

    const storedUser = JSON.parse(
      localStorage.getItem("@Provi:userData") || "null"
    );

    if (
      (formData.name === storedUser?.name &&
        formData.password === storedUser?.password) ||
      (formData.name === user?.name && formData.password === user?.password)
    ) {
      setUser(storedUser || user);
      history.push("/Dashboard/home");
    } else {
      alert("Credenciais inválidas!");
    }
  };

  return (
    <div className="login-page">
      <form onSubmit={handleLogin} className="form">
        <h2 className="login-title">Login</h2>

        <div className="input-data">
          <label>Login:</label>
          <input
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Type your user name"
            required
          />
        </div>

        <div className="input-data">
          <label>Password:</label>
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            placeholder="Type your user password"
            required
          />
        </div>

        <button type="submit">Enter</button>
        <p className="subtitle">
          Not have a account? <Link to="/Dashboard/register">Sign up</Link>
        </p>
      </form>
    </div>
  );
}

export default Login;
